.Landing {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  height: 100vh;
  color: #feda4a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 40px;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  text-align: center; }
  .Landing .opening-crawl {
    position: relative;
    top: 100vh;
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    -webkit-animation: crawl 120s linear;
            animation: crawl 120s linear;
    max-width: 800px; }
    .Landing .opening-crawl h2 {
      margin: 0 0 100px;
      text-transform: uppercase; }
    .Landing .opening-crawl .title {
      font-size: 90%;
      text-align: center; }
  .Landing .refresh-ico {
    height: 35px;
    width: 35px;
    position: absolute;
    right: 40px;
    top: 40px;
    z-index: 5;
    -webkit-filter: invert(0.6);
            filter: invert(0.6);
    cursor: pointer;
    transition: .2s; }
    .Landing .refresh-ico:hover {
      -webkit-filter: invert(1);
              filter: invert(1);
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }

@-webkit-keyframes crawl {
  0% {
    top: 130vh;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

@keyframes crawl {
  0% {
    top: 130vh;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }



